import { uniCard, uniSwiperDot, uniPagination, uniIcons, uniPopup } from '@dcloudio/uni-ui';
export default {
	components: {
		uniCard,
		uniPagination,
		uniIcons,
		uniPopup,
		uniSwiperDot
	},
	data() {
		return {
			title: 'Hello',
			extra: '教育科技公司',
			note: '',
			isFull: true,
			info: [
				{
					content: '内容 A'
				},
				{
					content: '内容 B'
				},
				{
					content: '内容 C'
				}
			],
			current: 0,
			mode: 'round'
		};
	},
	onLoad() {},
	methods: {
		open() {
			this.$refs.popup.open();
		},
		change(e) {
			this.current = e.detail.current;
		},
		clickBtn(){
			console.log(324234234);
		}
	}
};
